/* General styles for the container */
.container {
  max-width: 400px;
  margin: auto;
  background: #ffffff;
  padding: 30px;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  font-family: "Arial", sans-serif;
}

/* Header styling */
h2 {
  text-align: center;
  color: #333333;
  margin-bottom: 20px;
  font-weight: bold;
}

/* Label styling */
form label {
  font-weight: bold;
  color: #555555;
}

/* Input field styling */
form input {
  border: 1px solid #dddddd;
  border-radius: 5px;
  padding: 10px;
  font-size: 14px;
  width: 100%;
  box-sizing: border-box;
  transition: border-color 0.3s ease-in-out;
}

form input:focus {
  border-color: #007bff;
  outline: none;
  box-shadow: 0 0 5px rgba(0, 123, 255, 0.5);
}

/* Button styling */
button {
  width: 100%;
  padding: 10px;
  font-size: 16px;
  font-weight: bold;
  color: #ffffff;
  background: #007bff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease-in-out;
}

button:hover {
  background-color: #0056b3;
}

/* Responsive styling */
@media (max-width: 768px) {
  .container {
    padding: 20px;
  }

  button {
    font-size: 14px;
  }
}
