/* Googlefont Poppins CDN Link */
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}
.sidebar {
  position: fixed;
  height: 100%;
  width: 260px;
  background: #fafafa;
  box-shadow: 4px 0px 10px rgb(0 0 0 / 10%);
  transition: 0.2s ease-in;
  color: #333;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.sidebar::-webkit-scrollbar {
  width: 0.5em;
}

.sidebar .logo-details {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0;
  margin-bottom: 20px !important;
  box-shadow: 4px 0px 10px rgb(0 0 0 / 10%);
  min-height: 70px;
  background-color: #fff;
  font-weight: 700;
  font-size: 22px;
  color: #7c868b;
}

.info {
  margin-bottom: 3px !important;
}

.info.closed {
  width: 0;
  display: none;
}
.logo-details.closed {
  width: 0;
  display: none;
}
.sidebar.open {
  width: 250px;
  transition: width 0.3s ease;
}

.sidebar.closed {
  width: 70px;
  transition: width 0.3s ease;
}

.sidebar .logo-details span {
  display: flex;
  margin-left: 10px;
  color: #7c868b;
}
.logo-details {
  margin-bottom: 20px;
}
.logout {
  display: flex;
  justify-content: center;
  align-items: center;
}

.sidebar .nav-links li {
  position: relative;
  list-style: none;
  height: 55px;
  margin-bottom: 10px;
}

.sidebar .nav-links li a {
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  text-decoration: none;
  transition: all 0.4s ease;
  cursor: pointer;
  color: #11142d;
  border-radius: 4px;
  line-height: 25px;
}
.sidebar .nav-links li a.active {
  background: #c4e7ff;
  color: #38abf0;
}

.sidebar .nav-links li a .iconify {
  color: #0d0d0e;
  width: 40px;
  height: 40px;
  background-color: #b1e5f8;
  padding: 10px;
  border-radius: 9px;
}
.sidebar .nav-links li i {
  min-width: 80px;
  text-align: center;
  font-size: 18px;
}
.layout-container {
  display: grid;
  grid-template-columns: 250px 1fr;
  height: 100vh;
  overflow: hidden;
}

.sidebar .logo {
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 1rem;
}

.main-content {
  margin-left: 250px;
  width: 100%;
  padding: 2rem;
  overflow-y: auto;
  background-color: #f7f3ee;
}
