.chart {
  box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px;
  border-radius: 12px;
  padding: 20px;
  background-color: #ffffff;
}

.chart-button button {
  position: fixed;
  bottom: 40px;
  right: 40px;
  border: none;
  width: 200px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  z-index: 1000;
  color: black;
  background-color: #b1e5f8;
  font-size: 11px;
}

.chart-button button:hover {
  background-color: #b1e5f8;
}
